@import "./styles.scss";

.contentContainer {
  display: flex;
  flex-direction: column;
  height: 100%;
  width:  100%;
}

$aspirinLogoHeightLandscape: calc(1*20vh);
$aspirinLogoWidthLandscape: calc(1.09*20vh);
$aspirinLogoHeightPortrait: calc(1*30vw);
$aspirinLogoWidthPortrait: calc(1.09*30vw);
.questionAspirinLogo {
  height: $aspirinLogoHeightPortrait;
  width: $aspirinLogoWidthPortrait;
}
@media (orientation: landscape) {
  .questionAspirinLogo {
    height: $aspirinLogoHeightLandscape;
    width: $aspirinLogoWidthLandscape;
  }
}
.questionAspirinLogo {
  display: block;
  margin: auto;
  padding: 20px;
  flex-shrink: 0;
}

.navTest {
  height: 10vh;
  width: 100%;
  background: orange;
}

.buttonContainer {
  background: whitesmoke;
  height: 20vh;
  width: 100%;
  margin-top: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  box-shadow: 0px -10px 10px rgba(0,0,0,0.2);
}
@media (orientation: landscape) {
  .buttonContainer {
    height: 25vh;
  }
}

.buttonContainer.multipleRow {
  height: 50vh;
}

.numPadContainer {
  background: whitesmoke;
  // height: 50vh;
  width: 100%;
  margin-top: auto;
  display: flex;
  flex-direction: column;
}

$numPadNextButtonHeight: 50pt;
.numPadNextButton {
  background: $red-theme;
  width: 100%;
  height: $numPadNextButtonHeight;
  color: white;
  text-align: center;
  font-family: "Montserrat";
  font-size: 130%;
  line-height: $numPadNextButtonHeight;
  flex-shrink: 1;
  cursor: pointer;
}

.button {
  height: 16vh;
  width: 16vh;
  line-height: 16vh;
  font-size: 2vh;
  margin-left: 2vw;
  margin-right: 2vw;
  @media (orientation: landscape) {
      height: 20vh;
      width: 20vh;
      line-height: 20vh;
      font-size: 3vh;
    margin-top: 1vw;
    margin-bottom: 1vw;
    margin-left: 2vw;
    margin-right: 2vw;
  }
  border-radius: 100%;
  box-shadow: 0px -1vh 1vh rgba(0,0,0,0.2);
  color: white;
  background: $red-theme;
  text-align: center;
  cursor: pointer;
  user-select: none;
  font-family: "Montserrat";
  word-wrap: break-word;

  @include no-select();
}

.button:active {

  transform: scale(1.2, 1.2);
}

.topContainer {
  background: $red-theme;
  height: 50%;
  width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.inputTextContainer {
  height: 20%;
  width: 100%;
}

.inputText {
  font-size: 200%;
  text-align: center;
  @include vertical-align();
}

.changeUnitsContainer {
  height: 20%;
  width: 100%;
  cursor: pointer;
}

.changeUnitsText {
  color: white;
  opacity: 0.8;
  font-size: 150%;
  text-align: center;
  @include vertical-align;
}

.questionTextContainer {
  margin-left: 10%;
  margin-right: 10%;
}

$questionTextLeftMargin: 10%;
$questionTextRightMargin: 10%;
.questionText {
  font-family: "Montserrat";
  color: white;
  flex-grow: 1;
  overflow-y: auto;
  white-space: pre-wrap;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 120%;
  @media (orientation: landscape) {
    font-size: 170%;
    padding-left: 10%;
    padding-right: 10%;
  }
}

.questionTextCenter {
  text-align: center;
}

.textTopGradient {
  background-image: -webkit-gradient(
      linear, right top, right bottom, from($red-theme),
      to($red-theme-transparent)
  );
  width: 100%;
  height: 20px;
  margin-top: -20px;
  margin-left: -$questionTextLeftMargin;
  position: absolute;
}

span.reference {
  color: rgb(140, 200, 255);
  cursor: pointer;
  vertical-align: super;
  font-size: medium;
  font-weight: bold;
}

.container {
  margin: 0 auto;
  padding: 0 0 0px;
  height: 10vh;
  background: green;
  //overflow-y: scroll;
  //align-self: flex-end;
}



