@import "./styles.scss";

.citationContainer {
  background: black;
  position: absolute;
  flex-direction: column;
  bottom: 0;
  height: 40vh;
  width: 100vw;
}

.citation-enter {
  opacity: 0;
}

.citation-enter.citation-enter-active {
  opacity: 1;
  transition: opacity 0.3s linear;
}

.citation-leave {
  opacity: 1;
}
.citation-leave.citation-leave-active {
  opacity: 0;
  transition: opacity, 0.3s linear;
}

.citationText {
  color: white;
  font-family: Montserrat;
  font-size: 110%;
  @media (orientation: landscape) {
    font-size: 140%;
  }
  margin: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  overflow-y: auto;
  white-space: pre-wrap;
  height: 70%;
}

.citationCancelButton {
  height: 35px;
  width: 35px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  cursor: pointer;
}



