@import "./styles.scss";

$textLeftMargin: 20px;
$textRightMargin: 20px;
.textContentText {
  font-size: 110%;
  @media (orientation: landscape) {
    font-size: 140%;
  }
  font-family: "Montserrat";
  color: white;
  overflow-y: auto;
  white-space: pre-wrap;
  margin-left: $textLeftMargin;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-right: $textRightMargin
}

a {
    color: white;
}

.textContentBody {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.textContentContainer {
  background: $red-theme;
  flex: 1 1 auto;
  overflow-y: auto;
}

.textTopGradient {
  background-image: -webkit-gradient(
      linear, right top, right bottom, from($red-theme),
      to($red-theme-transparent)
  );
  width: 100%;
  height: 20px;
  margin-top: -20px;
  margin-left: -$textLeftMargin;
  position: absolute;
}




