@import "./styles.scss";

.root {
  background: $red-theme;
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
}

.navBar {
  width: 100%;
  height: 40px;
  display: flex;
  flex-shrink: 0;
}

.navBackButton {
  height: 100%;
  color: white;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-family: "Montserrat";
  @include no-select();
}

.navBackButtonText {
  padding-left: 5px;
}

.navBackButtonImage {
  padding-left: 5px;
  height: 25px;
  width: 12.5px;
}

.navInfoImage {
  height: 25px;
  width: 25px;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.navRightButtonContainer {
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
}



