@import "./styles.scss";

.numPadContentContainer {
  height: 100%;
  width: 100%;
  flex-direction: column;
  display: flex;
  justify-content: space-around;
}

.numPadRow {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

$buttonHeight: 10vh;
.numPadButton {
  cursor: pointer;
  color: $red-theme;
  height: $buttonHeight;
  width: 30%;
  text-align: center;
  font-size: 200%;
  font-family: "Montserrat";
  line-height: $buttonHeight;
  @include no-select();
}

.numPadButton.clear {
  font-size: 150%;
  font-family: "Montserrat";
}



