@import "./styles.scss";

.learnMoreRoot {
  background: $red-theme;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  white-space: pre-wrap;
}

.contentContainer {
  display: flex;
  flex-direction: column;
  height: 100%;
  width:  100%;
}

@media (min-aspect-ratio: 1/1) {
  .learnMoreAspirinLogo {
    height: 10vw;
    width: 10vw;
    display: block;
    margin: auto;
    margin-top: 2vw;
  }
}

@media (max-aspect-ratio: 1/1) {
  .learnMoreAspirinLogo {
    height: 20vw;
    width: 20vw;
    display: block;
    margin: auto;
    margin-top: 2vw;
  }
}

.learnMoreButton {
  box-shadow: 0px -10px 10px rgba(0,0,0,0.2);
  color: $red-theme;
  background: white;
  text-align: center;
  cursor: pointer;
  user-select: none;
  font-size: 100%;
  font-family: Montserrat;
  @include no-select();
}

@media (max-aspect-ratio: 1/1) {
  $buttonHeight: 15vw;
  .learnMoreButton {
    height: $buttonHeight;
    width: 70vw;
    margin: auto;
    margin-top: 3vw;
    margin-bottom: 3vw;
    border-radius: $buttonHeight;
    line-height: $buttonHeight;
    font-size: 4vw;
  }
}

@media (min-aspect-ratio: 1/1) {
  $buttonHeight: 7vw;
  .learnMoreButton {
    height: $buttonHeight;
    width: 50vw;
    margin: auto;
    margin-top: 2vw;
    margin-bottom: 2vw;
    border-radius: $buttonHeight;
    line-height: $buttonHeight;
    font-size: 2vw;
  }
}

.learnMoreButton:active {

  transform: scale(1.2, 1.2);
}




