@import "./styles.scss";

$disTextLeftMargin: 20px;
$disTextRightMargin: 20px;
.disclaimerText {
  height: 85%;
  font-size: 110%;
  @media (orientation: landscape) {
    font-size: 140%;
  }
  font-family: "Montserrat-Light";
  color: white;
  overflow-y: auto;
  white-space: pre-wrap;
  margin-left: $disTextLeftMargin;
  padding-top: 20px;
  margin-right: $disTextRightMargin;
}

.disclaimerRoot {
  background: rgba(0,0,0,0.7);
  position: absolute;
  width: 100%;
  height: 100%;
}

.disclaimerButtonContainer {
  background: $red-theme;
  position: absolute;
  width: 100%;
  height: 10%;
  bottom: 0%;
  display: flex;
}

.disclaimerButton {
  color: white;
  padding-left: 2%;
  padding-right: 2%;
  font-family: Montserrat;
  font-size: 130%;
  cursor: pointer;
}

.disclaimerAcceptButton {
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
}

.disclaimerDeclineButton {
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;

}



