@import "./styles.scss";

$recTextLeftMargin: 20px;
$recTextRightMargin: 20px;
.recommendationText {
  font-size: 110%;
  font-family: "Montserrat";
  color: white;
  overflow-y: auto;
  white-space: pre-wrap;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 120%;
  @media (orientation: landscape) {
    padding-left: 10%;
    padding-right: 10%;
    font-size: 150%;
  }
}

.recommendationBody {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.recommendationContentContainer {
  background: $red-theme;
  flex: 1 1 auto;
  overflow-y: auto;
}

.textTopGradient {
  background-image: -webkit-gradient(
      linear, right top, right bottom, from($red-theme),
      to($red-theme-transparent)
  );
  width: 100%;
  height: 20px;
  margin-top: -20px;
  margin-left: -$recTextLeftMargin;
  position: absolute;
}

span.reference {
  color: rgb(140, 200, 255);
  cursor: pointer;
  vertical-align: super;
  font-size: medium;
  font-weight: bold;
}

.recommendationButton {
  box-shadow: 0px -10px 10px rgba(0,0,0,0.2);
  color: $red-theme;
  background: white;
  text-align: center;
  cursor: pointer;
  user-select: none;
  flex-shrink: 0;
  font-family: Montserrat;
  @include no-select();
}

@media (max-aspect-ratio: 1/1) {
  $buttonHeight: 15vw;
  .recommendationButton {
    height: $buttonHeight;
    width: 70%;
    margin-top: 4vw;
    margin-bottom: 2vw;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 2vw;
    padding-left: 2vw;
    padding-right: 2vw;
    border-radius: $buttonHeight;
    line-height: $buttonHeight;
    font-size: 3vw;
  }
}

@media (min-aspect-ratio: 1/1) {
  $buttonHeight: 8vw;
  .recommendationButton {
    height: $buttonHeight;
    width: 50vw;
    margin: auto;
    margin-top: 2vw;
    margin-bottom: 2vw;
    border-radius: $buttonHeight;
    line-height: $buttonHeight;
    font-size: 2vw;
  }
}



