/* Variables */
$red-theme: rgb(209, 51, 66);
$red-theme-transparent: rgba(209, 51, 66, 0);

/* Styles */
//reset css: avoids strage override somewhere keep at top of page
@font-face {
  font-family: Montserrat;
  src: url('../fonts/Montserrat-Regular.ttf');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: Montserrat;
  src: url('../fonts/Montserrat-Bold.ttf');
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: Montserrat-Light;
  src: url('../fonts/Montserrat-Light.ttf');
  font-style: normal;
  font-weight: normal;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

html, body {margin: 0; height: 100%; overflow: hidden;touch-action: none}

@mixin vertical-align {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

@mixin no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -khtml-user-select: none;    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;           /* Non-prefixed version, currently
                                  not supported by any browser */
}

.hidden {
  display: none;
}

.emphasis {
  text-decoration: underline;
}

.push-enter {
  left: 100%;
  opacity: 1;
}

.push-enter.push-enter-active {
  left: 0;
  transition: left 0.3s linear;
}

.push-leave {
  right: 0%;
  opacity: 1;
}
.push-leave.push-leave-active {
  right: 100%;
  opacity: 1;
  transition: right, opacity, 0.3s linear;
}

.pop-enter.pop-enter-active {
  right: 0%;
  opacity: 1;
  transition: right, opacity, 0.3s linear;
}

.pop-enter {
  right: 100%;
  opacity: 1;
}

.pop-leave {
  left: 0%;
  opacity: 1;
}
.pop-leave.pop-leave-active {
  left: 100%;
  opacity: 1;
  transition: left, opacity, 0.3s linear;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 0.3s linear;
}

.fade-leave {
  opacity: 1;
}
.fade-leave.fade-leave-active {
  opacity: 0;
  transition: opacity, 0.3s linear;
}



