@import "./styles.scss";

$notSupportedTextLeftMargin: 20px;
$notSupportedTextRightMargin: 20px;
.notSupportedText {
  height: 85%;
  font-size: 110%;
  @media (orientation: landscape) {
    font-size: 140%;
  }
  font-family: "Montserrat-Light";
  color: white;
  overflow-y: auto;
  white-space: pre-wrap;
  margin-left: $notSupportedTextLeftMargin;
  padding-top: 20px;
  margin-right: $notSupportedTextRightMargin;
}

.notSupportedRoot {
  background: $red-theme;
  position: absolute;
  width: 100%;
  height: 100%;
}




